*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  @media (max-width: 400px) {
    font-size: 40%;
  }
}
body {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}
a {
  display: block;
  text-decoration: none;
}
ul {
  list-style: none;
}
img {
  display: block;
  height: 100%;
  width: 100%;
}